import { Component, h, Prop } from '@stencil/core';
import { Articles } from '../../../global/data/content';

@Component({
	tag: 'ks-content-articles',
	styleUrl: 'articles.css'
})
export class ContentArticles {
    @Prop() content: Articles;

    render() {
        return this.content.articles.map(article =>
            <ks-article-card flat
                link={article.link}
                heading={article.heading}
                description={article.description}
                image={article.image}
                webp={article.webp}
                date={article.date}
                views={article.views}
            />
        );
    }
}